import { motion } from "framer-motion";
// --- CHANGE 1: Fix the imports ---
import React, { useState, useEffect, useMemo } from "react";
import { useTheme } from "./ThemeContext"; // <-- CORRECT: Import your custom hook
import "./App.css";
import CryptoJS from "crypto-js";
import {
  Button,
  Card,
  CardContent,
  Typography,
  Box,
  MenuItem,
  Select,
  FormControl,
  InputLabel,
  CircularProgress,
  ThemeProvider,
  createTheme,
  CssBaseline, // <-- CORRECT: Import CssBaseline
  Snackbar,
} from "@mui/material";
import CheckCircleIcon from "@mui/icons-material/CheckCircle";
import HighlightOffIcon from "@mui/icons-material/HighlightOff";
import ThemeToggleButton from "./ThemeToggleButton";
import QuestionFillAnimation from "./QuestionFillAnimation";

// Renamed Quiz to App to reflect its new role as the main component
function App() {
  const [questions, setQuestions] = useState([]);
  const [currentQuestion, setCurrentQuestion] = useState(0);
  const [score, setScore] = useState(0);
  const [showScore, setShowScore] = useState(false);
  const [selectedAnswer, setSelectedAnswer] = useState(null);
  const [feedback, setFeedback] = useState(null);
  const [selectedSection, setSelectedSection] = useState("01");
  const [selectedPart, setSelectedPart] = useState("001");
  const [loading, setLoading] = useState(false);
  const [highlightCorrectAnswer, setHighlightCorrectAnswer] = useState(false);
  const [error, setError] = useState(false);
  const [isHovering, setIsHovering] = useState(false);
  const [intervalNm, setIntervalNm] = useState(null);
  // --- CHANGE 2: This will now correctly receive 'light' or 'dark' from the context ---
  const { mode } = useTheme();

  const sections = [
    {
      value: "01",
      label: "Group 1",
      parts: 3,
      type: "fill-in",
    },
  ];

  const partsForSelectedSection = selectedSection
    ? Array.from(
        { length: sections.find((s) => s.value === selectedSection).parts },
        (_, index) => ({
          value: (index + 1).toString().padStart(3, "0"),
          label: `Part ${index + 1}`,
        })
      )
    : [];

  const loadQuestions = async (section, part) => {
    setLoading(true);
    setError(false);
    try {
      const encryptedModule = await import(
        `./encryptedQuestions${section}${part}.json`
      );
      const secretKey = "your-secret-key";
      const decryptedData = CryptoJS.AES.decrypt(
        encryptedModule.default.data,
        secretKey
      ).toString(CryptoJS.enc.Utf8);
      setQuestions(JSON.parse(decryptedData));
      setCurrentQuestion(0);
      setScore(0);
      setShowScore(false);
    } catch (err) {
      console.error("Error loading or decrypting questions:", err);
      setError(true);
      setQuestions([]);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    if (selectedPart && selectedSection) {
      loadQuestions(selectedSection, selectedPart);
    }
  }, [selectedPart, selectedSection]);

  const handleReturnToPart = () => {
    setSelectedPart("");
    setQuestions([]);
    setShowScore(false);
  };

  const handleReturnToSection = () => {
    setSelectedSection("");
    setSelectedPart("");
    setQuestions([]);
    setShowScore(false);
  };

  const theme = useMemo(
    () =>
      createTheme({
        brand: {
          light: "#F8F9FA",
          dark: "#0D1117",
          primary: "#007FFF", // A vibrant blue
          secondary: "#FF6F61", // A coral/orange
          paper_light: "#FFFFFF",
          paper_dark: "#3b6ab6ff", 
          background_light: "#F8F9FA",
          background_dark: "#0D1117", 
        },
        palette: {
          mode, // This now dynamically switches between 'light' and 'dark'
          primary: {
            main: "#1976d2",
          },
          secondary: {
            main: "#9C27B0",
          },
        },
      }),
    [mode]
  );

  if (loading)
    return (
      <Box
        display="flex"
        justifyContent="center"
        alignItems="center"
        minHeight="100vh"
      >
        <CircularProgress />
      </Box>
    );

  // Note: For a complete solution, you should also wrap these selection screens in the ThemeProvider
  // so they also get the theme. The simplified approach is to move the ThemeProvider
  // to wrap the entire return statement. I've done that below.

  // Awaiting data load after selection
  if (questions.length === 0 && selectedPart) {
    return (
      <Box
        display="flex"
        justifyContent="center"
        alignItems="center"
        minHeight="100vh"
      >
        <CircularProgress />
      </Box>
    );
  }

  const currentSectionConfig = sections.find(
    (s) => s.value === selectedSection
  );

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ duration: 1 }}
      >
        <Box sx={{ position: "absolute", top: 16, right: 16, zIndex: 1300 }}>
          <ThemeToggleButton />
        </Box>

        <Box
          display="flex"
          flexDirection="column" // Changed to column for card + buttons layout
          justifyContent="center"
          alignItems="center"
          minHeight="100vh"
          gap={2} // Adds a little space between the card and buttons
        >
          {/* --- CONDITIONAL RENDERING --- */}
          {!selectedSection ? (
            <Card sx={{ width: 500, p: 3 }}>
              <CardContent>
                <Typography variant="h5" gutterBottom>
                  Select a Group
                </Typography>
                <FormControl fullWidth>
                  <InputLabel id="select-section-label">
                    Select Group
                  </InputLabel>
                  <Select
                    labelId="select-section-label"
                    value={selectedSection}
                    onChange={(e) => setSelectedSection(e.target.value)}
                    label="Select Section"
                  >
                    {sections.map((section) => (
                      <MenuItem key={section.value} value={section.value}>
                        {section.label}
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
              </CardContent>
            </Card>
          ) : !selectedPart ? (
            <Card sx={{ width: 500, p: 3 }}>
              <CardContent>
                <Typography variant="h5" gutterBottom>
                  Select a Part from Group {selectedSection}
                </Typography>
                <FormControl fullWidth>
                  <InputLabel id="select-part-label">Select Part</InputLabel>
                  <Select
                    labelId="select-part-label"
                    value={selectedPart}
                    onChange={(e) => setSelectedPart(e.target.value)}
                    label="Select Part"
                  >
                    {partsForSelectedSection.map((part) => (
                      <MenuItem key={part.value} value={part.value}>
                        {part.label}
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
              </CardContent>
            </Card>
          ) : error ? (
            <Box
              display="flex"
              flexDirection="column"
              justifyContent="center"
              alignItems="center"
            >
              <Typography color="error">Error loading questions.</Typography>
              <Button onClick={handleReturnToPart} sx={{ mt: 2 }}>
                Go Back
              </Button>
            </Box>
          ) : (
            // --- MAIN QUIZ VIEW ---
            // Use a React Fragment to return multiple elements at the same level
            <>
              <Card
                sx={{
                  width: { xs: "95%", sm: "80%", md: "700px" },
                  maxWidth: "90vw",
                  p: { xs: 1, sm: 2, md: 3 },
                  overflowY: "auto",
                }}
              >
                <CardContent>
                  {currentSectionConfig.type === "fill-in" ? (
                    // Question component renders here without the buttons
                    <QuestionFillAnimation data={questions} hintuse={true} />
                  ) : (
                    <Typography>MCQ Quiz Area</Typography>
                  )}
                </CardContent>
              </Card>

              {/* --- FIX: THE BUTTONS ARE NOW HERE --- */}
              {/* Placed outside and below the Card for better layout */}
              <Box
                display="flex"
                justifyContent="space-between"
                mt={2}
                width={{ xs: "95%", sm: "80%", md: "700px" }}
                maxWidth="90vw"
              >
                <Button
                  variant="contained"
                  color="secondary"
                  onClick={handleReturnToPart}
                  sx={{ textTransform: "none" }}
                >
                  Select Another Part
                </Button>
                <Button
                  variant="contained"
                  color="secondary"
                  onClick={handleReturnToSection}
                  sx={{ textTransform: "none" }}
                >
                  Select Another Group
                </Button>
              </Box>
            </>
          )}
        </Box>
      </motion.div>
    </ThemeProvider>
  );
}

export default App;
