// SingleQuestionAnimation.js (Definitively Corrected for Looping)
import { motion } from "framer-motion";
import React, { useEffect, useRef, useState, useMemo } from "react";
import AnimatedWord from "./AnimatedWord";

// --- Configuration --- (no changes)
const DEFAULT_ANIMATION_TYPE = "svg-curve";
const RESIZE_ANIMATION_TYPE = "spring";
const animationInterval = 6500;
const delayBeforeStartMeasurement = 100;
const resizeDebounceDelay = 250;
const HINT_LENGTH = 3;
const ANIMATION_DURATION_MS = 14200;
const GRACE_PERIOD_MS = 50;

const BLANK_REGEX = /(_BF_\d+)/g;
const BLANK_TEST_REGEX = /^_BF_\d+$/;

// <<< 2. Define the animation variants for our container.
// This is a clean way to describe the animation states.
const containerVariants = {
  // The state before the component mounts
  hidden: {
    opacity: 0,
    scale: 0.98, // A subtle zoom for a nicer effect
  },
  // The state the component will animate to
  visible: {
    opacity: 1,
    scale: 1,
    transition: {
      duration: 1.5, // The fade-in will last 1.5 seconds
      ease: "easeInOut",
    },
  },
};

const SingleQuestionAnimation = ({
  data,
  hintuse = false,
  onAllAnimationsComplete = () => {},
}) => {
  // --- State & Refs --- (no changes)
  const containerRef = useRef(null);
  const blankRefs = useRef({});
  const [fillTargets, setFillTargets] = useState({});
  const [containerRect, setContainerRect] = useState(null);
  const [windowTrigger, setWindowTrigger] = useState(0);
  const [hasResized, setHasResized] = useState(false);
  const [animatedKeys, setAnimatedKeys] = useState(new Set());
  const [completedKeys, setCompletedKeys] = useState({});

  useEffect(() => {
    setCompletedKeys({});
    setAnimatedKeys(new Set());
    // Also reset targets to ensure fresh measurements for new data
    setFillTargets({});
  }, [data]);

  const handleAnimationEnd = (key) => {
    setCompletedKeys((prevKeys) => ({
      ...prevKeys,
      [key]: true,
    }));
  };

  const parsedFills = useMemo(() => {
    if (!data || !data.Fill) return [];
    return data.Fill.split(",")
      .map((entry) => {
        const trimmedEntry = entry.trim();
        const lastUnderscoreIndex = trimmedEntry.lastIndexOf("_BF_");
        if (lastUnderscoreIndex === -1)
          return { text: trimmedEntry, key: null };
        const text = trimmedEntry.substring(0, lastUnderscoreIndex);
        const key = trimmedEntry.substring(lastUnderscoreIndex);
        return { text, key };
      })
      .filter((fill) => fill.key !== null);
  }, [data?.Fill]);

  const measurePositions = () => {
    if (!containerRef.current) return;
    const currentContainerRect = containerRef.current.getBoundingClientRect();
    setContainerRect(currentContainerRect);
    const targets = {};
    let allRefsFound = true;
    parsedFills.forEach(({ key }) => {
      const el = blankRefs.current[key];
      if (el) {
        const rect = el.getBoundingClientRect();
        targets[key] = {
          x: rect.left - currentContainerRect.left,
          y: rect.top - currentContainerRect.top,
        };
      } else {
        // If a ref is missing, we know we can't animate everything yet.
        allRefsFound = false;
      }
    });

    // Only set targets if we have at least one, to avoid triggering effects with empty data.
    if (Object.keys(targets).length > 0) {
      setFillTargets(targets);
    }
  };

  // <<< THE FIX: This is the new, robust completion check. >>>
  useEffect(() => {
    // The number of animations we EXPECT to run is the number of targets we successfully measured.
    const totalPossibleAnimations = Object.keys(fillTargets).length;
    const totalCompletedAnimations = Object.keys(completedKeys).length;

    // Only fire the completion signal if:
    // 1. We have successfully measured at least one target.
    // 2. The number of completed animations is equal to the number of animations we could possibly run.
    if (
      totalPossibleAnimations > 0 &&
      totalCompletedAnimations === totalPossibleAnimations
    ) {
      onAllAnimationsComplete();
    }
    // This effect now correctly depends on `fillTargets` as the source of truth for what's possible.
  }, [completedKeys, fillTargets, onAllAnimationsComplete]);

  useEffect(() => {
    const timeoutId = setTimeout(measurePositions, delayBeforeStartMeasurement);
    return () => clearTimeout(timeoutId);
  }, [data?.Ans, windowTrigger]);

  useEffect(() => {
    let debounceTimeoutId = null;
    const handleResize = () => {
      clearTimeout(debounceTimeoutId);
      debounceTimeoutId = setTimeout(() => {
        setHasResized(true);
        setWindowTrigger((prev) => prev + 1);
      }, resizeDebounceDelay);
    };
    window.addEventListener("resize", handleResize);
    return () => {
      window.removeEventListener("resize", handleResize);
      clearTimeout(debounceTimeoutId);
    };
  }, []);

  useEffect(() => {
    const timerIDs = [];
    parsedFills.forEach(({ key }, index) => {
      const startFadeDelay = GRACE_PERIOD_MS + animationInterval * index;
      const id = setTimeout(() => {
        setAnimatedKeys((prevKeys) => new Set(prevKeys).add(key));
      }, startFadeDelay);
      timerIDs.push(id);
    });
    return () => {
      timerIDs.forEach((id) => clearTimeout(id));
    };
  }, [parsedFills]);

  // --- Rendering Logic
  const currentAnimationType = hasResized
    ? RESIZE_ANIMATION_TYPE
    : DEFAULT_ANIMATION_TYPE;
  const renderedQues = useMemo(
    () => data?.Ques?.replace(/_BF_\d+/g, "") || "",
    [data?.Ques]
  );
  const initialWordPositions = useMemo(() => {
    const startY = containerRect ? containerRect.height + 10 : 180;
    return parsedFills.map((_, index) => ({ x: 10 + index * 100, y: startY }));
  }, [parsedFills, containerRect]);

  if (!data) return <div>Loading data...</div>;

  return (
    // <<< 3. Change the main wrapper div to motion.div and add the animation props
    <motion.div variants={containerVariants} initial="hidden" animate="visible">
      <h3>Question</h3>
      <p>{renderedQues}</p>
      <h3>Answer</h3>

      <div
        ref={containerRef}
        style={{
          position: "relative",
          minHeight: "220px",
          border: "1px solid #ccc",
          padding: "10px",
          overflow: "hidden",
        }}
      >
        <p style={{ whiteSpace: "pre-wrap", margin: 0, lineHeight: "1.8" }}>
          {data.Ans.split(BLANK_REGEX).map((part, i) => {
            if (BLANK_TEST_REGEX.test(part)) {
              const key = part;
              if (!blankRefs.current[key]) blankRefs.current[key] = null;
              let content = "\u00A0";
              if (hintuse) {
                const fillData = parsedFills.find((f) => f.key === key);
                if (fillData) {
                  const hintWrapperStyle = {
                    display: "inline-block",
                    transition: `opacity ${
                      ANIMATION_DURATION_MS / 1000
                    }s ease-out`,
                    opacity: animatedKeys.has(key) ? 0 : 1,
                  };
                  const fullAnswerText = fillData.text;
                  const hintText = fullAnswerText.substring(0, HINT_LENGTH);
                  const underscoreCount = Math.max(
                    0,
                    fullAnswerText.length - hintText.length
                  );
                  content = (
                    <div style={hintWrapperStyle}>
                      {" "}
                      <span style={{ color: "Silver", fontWeight: "500" }}>
                        {" "}
                        {hintText}{" "}
                      </span>{" "}
                      <span style={{ letterSpacing: "3px", color: "#777" }}>
                        {" "}
                        {"_".repeat(underscoreCount)}{" "}
                      </span>{" "}
                    </div>
                  );
                }
              }
              return (
                <span
                  key={`${key}-${i}`}
                  ref={(el) => {
                    blankRefs.current[key] = el;
                  }}
                  style={{
                    display: "inline-block",
                    minWidth: "80px",
                    borderBottom: "1px solid black",
                    margin: "0 5px",
                    padding: "0 2px",
                    verticalAlign: "bottom",
                    textAlign: "left",
                    lineHeight: "1.72",
                  }}
                >
                  {" "}
                  {content}{" "}
                </span>
              );
            }
            return <span key={`text-${i}`}>{part}</span>;
          })}
        </p>
        {parsedFills.map(({ text, key }, index) => {
          const target = fillTargets[key];
          const initialPos = initialWordPositions[index];
          if (!target) {
            return null;
          }
          if (completedKeys[key]) {
            return (
              <div
                key={key}
                style={{
                  position: "absolute",
                  left: `${target.x}px`,
                  top: `${target.y}px`,
                  fontSize: "18px",
                  color: "dodgerblue",
                }}
              >
                {" "}
                {text}{" "}
              </div>
            );
          }
          return (
            <AnimatedWord
              key={`${key}-${currentAnimationType}-${windowTrigger}`}
              text={text}
              targetX={target.x}
              targetY={target.y}
              initialX={initialPos.x}
              initialY={initialPos.y}
              startDelay={GRACE_PERIOD_MS + animationInterval * index}
              animationType={currentAnimationType}
              containerWidth={containerRect?.width}
              containerHeight={containerRect?.height}
              onAnimationEnd={() => handleAnimationEnd(key)}
            />
          );
        })}
      </div>
    </motion.div>
  );
};

export default SingleQuestionAnimation;
