// QuestionFillAnimation.js
import React, { useState, useEffect, useRef } from "react";
import SingleQuestionAnimation from "./SingleQuestionAnimation"; // Import the component that animates one question
import { Button } from "@mui/material";
const PAUSE_BEFORE_LOOP_MS = 2500; // The "little bit of pause" (2.5 seconds)

const QuestionFillAnimation = ({ data }) => {
  const [currentQuestionIndex, setCurrentQuestionIndex] = useState(0);
  // <<< 1. State to trigger re-animation via the `key` prop.
  const [animationTrigger, setAnimationTrigger] = useState(0);

  // <<< 2. A ref to hold the timer ID for the loop, so we can clear it.
  const loopTimerRef = useRef(null);
  // <<< 3. This effect is crucial for cleanup.
  // It runs whenever the question changes, clearing any pending loop timer.
  useEffect(() => {
    // When the question index changes, we must cancel any scheduled replay
    // for the *previous* question.
    clearTimeout(loopTimerRef.current);

    // Also serves as a cleanup for when the component unmounts.
    return () => {
      clearTimeout(loopTimerRef.current);
    };
  }, [currentQuestionIndex]); // This effect depends only on the question index.

  // Ensure data is valid before proceeding
  if (!Array.isArray(data) || data.length === 0) {
    return <div>No valid question data provided.</div>;
  }

  // Function to go to the next question
  const handleNext = () => {
    // Only increment if not already at the last question
    if (currentQuestionIndex < data.length - 1) {
      setCurrentQuestionIndex((prevIndex) => prevIndex + 1);
      // Reset the trigger key for the new question.
      setAnimationTrigger(0);
    }
  };

  // Function to go to the previous question
  const handlePrevious = () => {
    // Only decrement if not already at the first question
    if (currentQuestionIndex > 0) {
      setCurrentQuestionIndex((prevIndex) => prevIndex - 1);
      // Reset the trigger key for the new question.
      setAnimationTrigger(0);
    }
  };
  // <<< 4. The callback that receives the signal from the child.
  const handleAllAnimationsComplete = () => {
    // When the animation is done, schedule a replay after a pause.
    loopTimerRef.current = setTimeout(() => {
      // Incrementing the trigger will change the child's key, forcing a re-mount.
      setAnimationTrigger((prev) => prev + 1);
    }, PAUSE_BEFORE_LOOP_MS);
  };

  // Get the data for the currently selected question
  const currentQuestionData = data[currentQuestionIndex];

  // Determine if buttons should be disabled
  const isFirstQuestion = currentQuestionIndex === 0;
  const isLastQuestion = currentQuestionIndex === data.length - 1;
  const showHints = true; // Assuming you want to use hints, set this as needed
  return (
    <div
      style={{
        padding: "20px",
        border: "1px solid lightgray",
        borderRadius: "8px",
      }}
    >
      {/* Display the current question number */}
      <div
        style={{
          marginBottom: "15px",
          textAlign: "center",
          fontWeight: "bold",
        }}
      >
        Question {currentQuestionIndex + 1} of {data.length}
      </div>

      {/* Render ONLY the current question's animation component */}
      {/* IMPORTANT: Use a key that changes when the question changes.
                 This tells React to unmount the old component instance and mount a new one,
                 effectively resetting the animation state for the new question.
                 Using the index or a unique ID from the data works well. */}
      <SingleQuestionAnimation
        // key={currentQuestionData.id || currentQuestionIndex} // Force re-mount on change
        // <<< 5. The magic `key` prop. Changing this forces a full reset and re-animation.
        key={`${currentQuestionIndex}-${animationTrigger}`}
        data={currentQuestionData}
        hintuse={showHints}
        // <<< 6. Pass the callback down to the child.
        onAllAnimationsComplete={handleAllAnimationsComplete}
      />

      {/* Navigation Buttons */}
      <div
        style={{
          marginTop: "20px",
          display: "flex",
          justifyContent: "space-between",
        }}
      >
        <Button
          variant="contained"
          color="primary"
          onClick={handlePrevious}
          disabled={isFirstQuestion}
          style={{
            textTransform: "none", // <-- Add this line
            padding: "8px 16px",
            cursor: isFirstQuestion ? "not-allowed" : "pointer",
          }}
        >
          Previous
        </Button>
        <Button
          onClick={handleNext}
          variant="contained"
          color="primary"
          disabled={isLastQuestion}
          style={{
            textTransform: "none", // <-- Add this line
            padding: "8px 16px",
            cursor: isLastQuestion ? "not-allowed" : "pointer",
          }}
        >
          Next
        </Button>
      </div>
    </div>
  );
};

export default QuestionFillAnimation;
