// AnimatedWord.js
import React, { useEffect, useState, useMemo } from "react";
import { catmullRom2Bezier } from "./utils"; // Ensure this path is correct

// --- Configuration ---
const DEFAULT_DURATION_MS = 6200; // Duration for the path travel of each letter
const CURVE_TENSION = 4.3;
const CURVE_VERTICAL_OFFSET = -90;
const LETTER_STAGGER_MS = 50; // Delay between each letter *starting* its travel/fade-in
const CROSSFADE_DURATION_MS = 300; // Duration of the fade between animating letters and final word

/** Generates points... (keep function as before) */
const generateCurvePoints = (startX, startY, endX, endY, verticalOffset) => {
  const dx = endX - startX;
  if (
    typeof startX !== "number" ||
    typeof startY !== "number" ||
    typeof endX !== "number" ||
    typeof endY !== "number"
  ) {
    return [];
  }
  return [
    { x: startX, y: startY },
    {
      x: startX + dx * Math.random() + 0.15,
      y: Math.min(startY, endY) + verticalOffset,
    },
    {
      x: startX + dx * Math.random() + 0.5,
      y: Math.min(startY, endY) + verticalOffset,
    },
    // { x: startX + dx * 0.25, y: Math.min(startY, endY) + verticalOffset },
    // { x: startX + dx * 0.75, y: Math.min(startY, endY) + verticalOffset },
    { x: endX, y: endY },
  ];
};

function AnimatedWord({
  text = "",
  initialX = 0,
  initialY = 0,
  targetX = null,
  targetY = null,
  startDelay = 0, // Delay before the *first* letter starts animating
  animationType = "svg-curve", // Animation type: 'pop-up', 'fly-in', 'spring-in', 'svg-curve'
  duration = DEFAULT_DURATION_MS, // Duration for letter travel
  textColor = "dodgerblue",
  fontSize = "18px",
  letterStagger = LETTER_STAGGER_MS,
  onAnimationEnd = () => {},
}) {
  const [isAnimating, setIsAnimating] = useState(false); // Starts the letter animations
  const [isFinishing, setIsFinishing] = useState(false); // Starts the crossfade to final word
  const [isVisible, setIsVisible] = useState(false); // Controls overall readiness/visibility

  const hasValidTarget =
    typeof targetX === "number" && typeof targetY === "number";
  const hasValidInitial =
    typeof initialX === "number" && typeof initialY === "number";
  const isCurveType = animationType === "svg-curve";

  // const characters = useMemo(
  //   ()  =>
  //    text.split(''), [text])

  const characters = useMemo(() => {
    const reversedString = text.split("").reverse().join(""); // "olleh"
    return reversedString.split(""); // Now this returns ['o', 'l', 'l', 'e', 'h']
  }, [text]);
  // Effect to start the main animation process
  useEffect(() => {
    if (!hasValidTarget || !hasValidInitial) {
      setIsAnimating(false);
      setIsFinishing(false);
      setIsVisible(false);
      return;
    }
    setIsVisible(true); // Mark as ready
    const startAnimTimer = setTimeout(() => {
      setIsAnimating(true);
    }, startDelay);

    return () => clearTimeout(startAnimTimer);
  }, [startDelay, hasValidTarget, hasValidInitial]);

  useEffect(() => {
    if (!isAnimating) return;

    let totalDuration = duration;
    if (animationType === "svg-curve") {
      const lastLetterStartTime = (characters.length - 1) * letterStagger;
      totalDuration = lastLetterStartTime + duration;
    }

    const endTimer = setTimeout(() => {
      onAnimationEnd();
    }, totalDuration);

    return () => clearTimeout(endTimer);
  }, [
    isAnimating,
    duration,
    animationType,
    characters,
    letterStagger,
    onAnimationEnd,
  ]);

  // Effect to start the 'finishing' (crossfade) phase
  useEffect(() => {
    if (!isAnimating || !isCurveType || characters.length === 0) {
      // Don't start finishing sequence if not animating, not curve type, or no text
      setIsFinishing(false); // Ensure finishing is reset if animation stops early
      return;
    }

    // Calculate when the *last* letter's animation is expected to *complete*
    // Last letter starts at: (characters.length - 1) * letterStagger
    // It takes 'duration' ms to travel the path.
    const lastLetterAnimEndTime =
      (characters.length - 1) * letterStagger + duration;

    // Start the crossfade slightly *before* the calculated end time for a smoother overlap
    const startCrossfadeDelay = Math.max(
      0,
      lastLetterAnimEndTime - CROSSFADE_DURATION_MS / 2
    );

    const finishTimer = setTimeout(() => {
      setIsFinishing(true);
    }, startCrossfadeDelay); // Use the relative delay from when isAnimating became true

    return () => clearTimeout(finishTimer);
    // Depend on isAnimating state and timing parameters
  }, [isAnimating, isCurveType, characters.length, letterStagger, duration]);

  // Calculate SVG path (only needed for curve type)
  const pathData = useMemo(() => {
    if (!isCurveType || !hasValidInitial || !hasValidTarget) return null;
    try {
      const points = generateCurvePoints(
        initialX,
        initialY,
        targetX,
        targetY,
        CURVE_VERTICAL_OFFSET
      );
      return points?.length >= 2
        ? catmullRom2Bezier(points, CURVE_TENSION)
        : null;
    } catch (error) {
      console.error("Error generating Catmull-Rom path:", error);
      return null;
    }
  }, [
    isCurveType,
    initialX,
    initialY,
    targetX,
    targetY,
    hasValidInitial,
    hasValidTarget,
  ]);

  // --- Style Calculation ---

  // Style for the individual ANIMATING LETTER SPANS (curve type)
  const getAnimatingLetterStyle = (index) => {
    if (!isCurveType) return {};

    const letterStartDelayMs = index * letterStagger; // Delay for this letter to *start*

    let style = {
      position: "absolute",
      left: "0px",
      top: "0px",
      fontSize: fontSize,
      color: textColor,
      whiteSpace: "pre",
      display: "inline-block",
      willChange: "offset-distance, opacity",
      zIndex: 10, // Animating letters on top during animation
      visibility: isVisible ? "visible" : "hidden",
      opacity: 0, // Start transparent
      offsetPath: pathData ? `path('${pathData}')` : "none",
      offsetDistance: "0%",
      transitionProperty: "offset-distance, opacity",
      transitionDuration: `${duration / 1000}s, ${duration / 1500}s`, // Offset animates full duration, opacity faster? Adjust as needed
      transitionTimingFunction: `cubic-bezier(${Math.random()}, ${Math.random()}, ${Math.random()}, ${Math.random()}, ${Math.random()}), ease-out`,
      // transitionTimingFunction: "cubic-bezier(1, 1.4, 0, 1.2, .1), ease-out",
      transitionDelay: `${letterStartDelayMs / 1000}s`, // Stagger start time
    };

    if (!pathData) {
      return { ...style, opacity: 0.5, visibility: "hidden" };
    }

    if (isAnimating) {
      // Target state for the main animation phase
      style.opacity = 1;
      style.offsetDistance = "100%";
    }

    if (isFinishing) {
      // During crossfade, fade out the animating letters
      style.opacity = 0;
      style.transitionProperty = "opacity"; // Only animate opacity now
      style.transitionDuration = `${CROSSFADE_DURATION_MS / 1000}s`;
      style.transitionTimingFunction = "ease-out";
      // No additional delay for fade out, it starts immediately when isFinishing is true
      style.transitionDelay = "0s";
    }

    return style;
  };

  // Style for the FINAL STATIC WORD DIV (curve type, shown during/after crossfade)
  const finalWordStyle = useMemo(() => {
    if (!isCurveType) return {};

    return {
      position: "absolute",
      left: hasValidTarget ? `${targetX}px` : "-9999px",
      top: hasValidTarget ? `${targetY}px` : "-9999px",
      fontSize: fontSize,
      color: textColor,
      whiteSpace: "nowrap",
      opacity: isFinishing ? 1 : 0, // Fade in when finishing
      visibility: isVisible ? "visible" : "hidden", // Base visibility
      zIndex: 9, // Slightly below animating letters initially
      willChange: "opacity",
      transition: `opacity ${CROSSFADE_DURATION_MS / 1000}s ease-in`,
      // No extra delay needed, starts when isFinishing becomes true
    };
  }, [
    isCurveType,
    isFinishing,
    isVisible,
    hasValidTarget,
    targetX,
    targetY,
    fontSize,
    textColor,
  ]);

  // Style for the WHOLE WORD DIV (used for NON-CURVE types)
  const nonCurveWordStyle = useMemo(() => {
    if (isCurveType) return {};
    // ... Copy non-curve style logic exactly from previous versions ...
    let style = {
      position: "absolute",
      fontSize: fontSize,
      color: textColor,
      whiteSpace: "nowrap",
      willChange: "transform, opacity, top, left",
      transition: `all ${duration / 1000}s ease-out`, // Use main duration prop
      opacity: isVisible ? 1 : 0,
      zIndex: 10,
      visibility: isVisible ? "visible" : "hidden",
    };
    if (!hasValidInitial || !hasValidTarget) {
      return { ...style, opacity: 0, visibility: "hidden", left: "-9999px" };
    }
    switch (animationType) {
      case "pop-up":
        style.left = `${targetX}px`;
        style.top = `${targetY}px`;
        style.transform = isAnimating ? "scale(1)" : "scale(0.3)";
        style.opacity = isAnimating ? 1 : 0;
        style.transition = `transform ${
          duration / 1000
        }s cubic-bezier(${Math.random()}, ${Math.random()}, ${Math.random()}, ${Math.random()}), opacity ${
          duration / 1000
        }s ease-out`;
        break;
      case "fly-in":
        style.left = isAnimating ? `${targetX}px` : `${initialX}px`;
        style.top = isAnimating ? `${targetY}px` : `${initialY}px`;
        style.opacity = isAnimating ? 1 : 0;
        style.transition = `left ${duration / 1000}s ease-out, top ${
          duration / 1000
        }s ease-out, opacity ${duration / 1000}s ease-out`;
        break;
      case "spring-in":
        style.left = `${targetX}px`;
        style.top = `${targetY}px`;
        style.transform = isAnimating ? "scale(1)" : "scale(0.1)";
        style.opacity = isAnimating ? 1 : 0;
        style.transition = `transform ${
          duration / 1000
        }s cubic-bezier(0.175, 0.885, 0.32, 1.5), opacity ${
          duration / 1000
        }s ease-out`;
        break;
      default:
        style.left = `${targetX}px`;
        style.top = `${targetY}px`;
        style.opacity = isAnimating ? 1 : 0;
        style.transition = `opacity ${duration / 1000}s ease-in`;
        break;
    }
    return style;
  }, [
    isCurveType,
    isAnimating,
    isVisible,
    hasValidInitial,
    hasValidTarget,
    animationType,
    duration,
    fontSize,
    textColor,
    initialX,
    initialY,
    targetX,
    targetY,
  ]);

  // --- Render Logic ---

  if (!isVisible) {
    return null; // Render nothing until ready
  }

  // --- Render CURVE type: Animating letters + Final static word for crossfade ---
  if (isCurveType) {
    return (
      <>
        {/* Debug Path Visualizer */}
        {/* {pathData && isVisible && ( <svg ... /> )} */}

        {/* Animating Letters */}
        {pathData &&
          characters.map((char, index) => (
            <span key={`anim-${index}`} style={getAnimatingLetterStyle(index)}>
              {char === " " ? "\u00A0" : char}
            </span>
          ))}

        {/* Final Static Word (for crossfade) */}
        {pathData && <div style={finalWordStyle}>{text}</div>}

        {/* Fallback if path failed */}
        {!pathData && hasValidTarget && (
          <div
            style={{
              position: "absolute",
              left: `${targetX}px`,
              top: `${targetY}px`,
              fontSize,
              color: textColor,
              opacity: isAnimating ? 1 : 0,
              transition: "opacity 0.3s",
            }}
          >
            {text} (Path Failed)
          </div>
        )}
      </>
    );
  }

  // --- Render NON-CURVE types: Single Div ---
  return <div style={nonCurveWordStyle}>{text}</div>;
}

export default AnimatedWord;
